#register-box{
    position: absolute;
    top: 50%;
    left:  50%;
    transform: translate(-50%, -50%);
    box-shadow: 0px 0px 20px 20px rgba(121, 223, 66, 0.684);
    width: 650px;
    border-radius: 20px;
    padding: 20px;
    background-color: white;
}
.reg-row-field{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.reg-row-field input{
    width: 300px;
}
.gender-field>h3{
    margin-bottom: 8px;
}
.gender-options{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.gender{
    display: flex;
    align-items: center;
    justify-content: start;
}
.gender input{
    margin-right: 10px;
}
#register-button input{
    width: 100%;
    margin-top: 30px;
    padding: 8px 0px;
    border: none;
    background: linear-gradient(to right,rgb(5, 237, 140),rgb(5, 239, 243),rgb(249, 4, 249));
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
}
.register-container{
    background: radial-gradient(rgb(5, 237, 140),rgb(5, 239, 243),rgb(249, 4, 249));
    width: 100vw;
    height: 100vh;

}