#login-box{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0px 0px 20px 20px rgba(128, 0, 128, 0.684);
    width: 350px;
    border-radius: 20px;
    padding: 20px
}
#login-title{
    margin-bottom: 20px;
}
.login-field-with-label{
    margin-bottom: 20px;
}
.login-label{
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}
.login-field{
    width: 100%;
    padding: 10px 5px;
    border: 1px solid purple;
    border-radius: 10px;
}
#login-button{
    background-color: purple;
    color: whitesmoke;
    border-radius: 10px;
    text-align: center;
    cursor: pointer;
}
#login-button input{
    width: 100%;
    padding: 10px 40px;
    background-color: transparent;
    border: none;
    color: whitesmoke;
    font-size: 20px;
}
#signup-field{
    text-align: center;
    margin-top: 30px;
    font-weight: bold;
    font-size: 16px;
}
#signup-field a{
    text-decoration: none;
    color: brown;
    font-size: 20px;
    font-style: italic;
}